/****************************************
Basic HTML element Overrides 
****************************************/
html,
body {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: black;
}
H2 {
  margin: 0;
}
/****************************************
CSS Variables
****************************************/
:root {
  --Padding: 10px;
  --Margin: var(--Padding);
  font-size: 16px;
}
/****************************************
Main App Container
****************************************/
#App {
  box-sizing: border-box;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
#App * {
  box-sizing: border-box;
}
/****************************************
HUD & Overlay 
****************************************/
.HUD {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.HUD section {
  height: 100%;
  width: 100%;
  display: grid;
  justify-items: center;
  align-items: center;
}
.HUD H2 {
  border: 1px solid black;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px;
}
.HUD.Main section {
  height: 100%;
  width: 100%;
  display: grid;
  justify-items: center;
  align-items: end;
}
